import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import IOSSwitch from "./iosSwitch";
import { useDispatch, useSelector } from "react-redux";
import { alarmAPI } from "../redux/alarmReducer";

const useStyles = makeStyles(() => ({
  root: {
    textAlign: "right",
    display: "flex",
    flexDirection: "row",
    justifyContent: 'flex-end',
    alignItems: 'center',
  },
  col: {
    display: "flex",
  },
}));

export default function AlarmSwitch() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const status = useSelector((state) => state.alarm.status);

  const handleChange = () => {
    dispatch(alarmAPI('PUT'))
  };

  return (
    <div className={classes.root}>
      <div className={classes.col}>Alarm</div>
      <div className={classes.col}>
        <IOSSwitch onChange={handleChange} checked={!(status === "off")} />
      </div>
    </div>
  );
}
