import '@fontsource/roboto';
import React, { useEffect } from 'react';
import { makeStyles, Paper } from '@material-ui/core'
import AlarmController from './components/alarm';
import Clock from './components/clock';
import LedController from './components/led';
import SleepTimerController from './components/sleepTimer';
import './socket';
import { ledAPI } from './redux/ledReducer';
import { alarmAPI } from './redux/alarmReducer';
import { sleepTimerAPI } from './redux/timerReducer';
import { useDispatch } from 'react-redux';

const useStyles = makeStyles(() => ({
  root: {
    maxHeight: "600px",
    width: "90%",
    maxWidth: "600px",
    margin: "auto",
    padding: '15px',
  },
}));

function App() {
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    console.log('hi');
    dispatch(ledAPI('GET'));
    dispatch(alarmAPI('GET'));
    dispatch(sleepTimerAPI('GET'));
  },[])

  return (
    <Paper elevation={4} className={classes.root}>
      <Clock />
      <LedController />
      <AlarmController />
      <SleepTimerController />
    </Paper>
  );
}

export default App;
