import { createSlice } from '@reduxjs/toolkit'
import { apiFactory } from '../requests';

export const ledSlice = createSlice({
  name: 'led',
  initialState: {
    status: 'off',
    brightness: 0
  },
  reducers: {
    setLed: (state, action) => {
        const {pwm} = action.payload;
        state.brightness = Math.round(pwm/255 * 100);

        if(pwm === 0) {
          state.status = 'off'
        } else {
          state.status = 'on'
        }
      }
    }
})

export const getBrightness = state => state.led.brightness;

export const { setLed } = ledSlice.actions;
export const ledAPI = apiFactory('led', setLed);

export default ledSlice.reducer