import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Slider from "@material-ui/core/Slider";
import { useDispatch, useSelector } from "react-redux";
import { ledAPI, getBrightness } from "../redux/ledReducer";

const useStyles = makeStyles({
  root: {
    display: 'flex',
    height: '100%',
    marginLeft: '20px'
  },
  slider: {
      display: 'flex',
      maxWidth: '350px',
  }
});

export default function PwmSlider() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const pwm = useSelector(getBrightness);
  const [val, setVal] = useState(pwm);

  useEffect(() => {
    setVal(pwm);
  },[pwm]);

  return (
    <div className={classes.root}>
      <Slider
        onChangeCommitted={(_, value) => {
          value = value/100 * 255;
          dispatch(ledAPI('POST', {brightness: value}))
        }}
        onChange={ (_, value) => setVal(value)}
        value={val}
        valueLabelDisplay="auto"
        step={1}
        min={0}
        max={100}
        className={classes.slider}
      />
    </div>
  );
}
