import { createSlice } from '@reduxjs/toolkit'
import { apiFactory } from '../requests.js';

export const timerSlice = createSlice({
    name: 'sleepTimer',
    initialState: {
        status: 'off',
        time: 30,
        fading: 10,
    },
    reducers: {
        setSleepTimer: (state, action) => {
            const { time, status, fading } = action.payload;

            state.time = time / 60000;
            state.status = status;
            state.fading = fading / 60000;
        },
    }
})

export const { setSleepTimer } = timerSlice.actions;

export const sleepTimerAPI = apiFactory('sleepTimer', setSleepTimer);

export default timerSlice.reducer