import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Slider from "@material-ui/core/Slider";
import { useDispatch, useSelector } from "react-redux";
import { sleepTimerAPI} from "../redux/timerReducer";

const useStyles = makeStyles({
  root: {
    display: 'flex',
    height: '100%',
    marginLeft: '20px'
  },
  slider: {
      display: 'flex',
      maxWidth: '350px',
  }
});

export default function SleepTimerTime() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const time = useSelector(state => state.sleepTimer.time);
  const fading = useSelector(state => state.sleepTimer.fading);
  const [val, setVal] = useState(time);

  useEffect(() => {
      setVal(time);
  }, [time]);

  return (
    <div className={classes.root}>
      <Slider
        onChangeCommitted={(_, value) => {
          value = value * 60000;
          dispatch(sleepTimerAPI('POST', {time: value}));
        }}
        onChange={ (_, value) => setVal(value)}
        value={val}
        valueLabelDisplay="auto"
        step={1}
        min={fading}
        max={90}
        className={classes.slider}
      />
    </div>
  );
}
