import React from "react";
import { Grid, makeStyles } from "@material-ui/core";
import { useEffect, useState } from "react";
import PropTypes from "prop-types";
import CircularProgress from '@material-ui/core/CircularProgress';
import Box from "@material-ui/core/Box";
import { useSelector } from "react-redux";

const useStyles = makeStyles(() => ({
  time: {
    textAlign: "center",
    padding: "10px",
    fontSize: "1.5rem",
    fontWeight: "bold",
  },
  date: {
    textAlign: "right",
  },
}));

const options = {
  weekday: "long",
  month: "numeric",
  day: "numeric",
  year: "numeric",
};

function CircularProgressWithLabel(props) {
  return (
    <Box position="relative" display="inline-flex">
      <CircularProgress size={85} style={{color: props.c}} variant="determinate" {...props} />
      <Box
        top={0}
        left={0}
        bottom={0}
        right={0}
        position="absolute"
        display="flex"
        alignItems="center"
        justifyContent="center"
      >
       {`${props.time.toLocaleTimeString('de-DE', {hour: '2-digit', minute: '2-digit'})}`}
      </Box>
    </Box>
  );
}

CircularProgressWithLabel.propTypes = {
  /**
   * The value of the progress indicator for the determinate variant.
   * Value between 0 and 100.
   */
  value: PropTypes.number.isRequired,
};

const progress = (value) => {
  let h = value.getHours();
  let min = value.getMinutes();
  return ((h * 60 + min) / 1440) * 100;
};

export default function Clock() {
  const classes = useStyles();
  const [time, setTime] = useState(new Date(Date.now()));
  const alarmStatus = useSelector( state => state.alarm.status)

  useEffect(() => {
    const iv = setInterval(() => setTime(new Date(Date.now())), 1000);

    return () => clearInterval(iv);
  }, []);

  return (
    <Grid container>
      <Grid item xs={12} className={classes.date}>
        {time.toLocaleDateString("de-DE", options)}
      </Grid>
      <Grid item xs={12} className={classes.time}>
      <CircularProgressWithLabel value={progress(time)} c={alarmStatus === 'off' ? '#dddddd' : '#f05454'} time={time}/>
      </Grid>
    </Grid>
  );
}
