import { io } from 'socket.io-client';
import { store } from './redux/store';

const socket = io();

socket.on('led', data => store.dispatch({
    type: 'led/setLed',
    payload: data
}))

socket.on('alarm', data => store.dispatch({
    type: 'alarm/setAlarm',
    payload: data
}))

socket.on('sleepTimer', data => store.dispatch({
    type: 'sleepTimer/setSleepTimer',
    payload: data
}))

export default socket;