import { createSlice } from '@reduxjs/toolkit'
import { apiFactory } from '../requests.js';

export const alarmSlice = createSlice({
    name: 'alarm',
    initialState: {
        status: 'off',
        time: Date.now(),
        fading: 20,
    },
    reducers: {
        setAlarm: (state, action) => state = action.payload,
        setTime: (state, action) => {
            state.time = action.payload;
            state.status = 'on'
        }
    }
})

export const { setAlarm } = alarmSlice.actions;
export const alarmAPI = apiFactory('alarm', setAlarm);

export default alarmSlice.reducer