import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import IOSSwitch from "./iosSwitch";
import { useDispatch, useSelector } from "react-redux";
import { sleepTimerAPI } from "../redux/timerReducer";

const useStyles = makeStyles(() => ({
  root: {
    textAlign: "right",
    display: "flex",
    flexDirection: "row",
    justifyContent: 'flex-end',
    alignItems: 'center',
  },
  col: {
    display: "flex",
  },
}));

export default function SleepTimerOnOff() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const status = useSelector((state) => state.sleepTimer.status);

  const handleChange = () => {
      dispatch(sleepTimerAPI('PUT'));
  };

  return (
    <div className={classes.root}>
      <div className={classes.col}>Sleep Timer</div>
      <div className={classes.col}>
        <IOSSwitch onChange={handleChange} checked={!(status === "off")} />
      </div>
    </div>
  );
}
