const port = window.location.hostname.includes('local') ? ':8000' : '';
const url = `http://${window.location.hostname}${port}/api`;

const requestFactory = path => async (method = "GET", data = '') => {
    if (method === 'GET') {
        return (await window.fetch(new Request(url + path + data, { method }))).json();
    }
    if (method === 'POST' || method === 'PUT') {
        data = data ? data : {};
        return (await window.fetch(new Request(url + path, {
            method,
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        }))).json();
    }
}

const endpoints = {
    led: requestFactory('/led'),
    alarm: requestFactory('/alarm'),
    sleepTimer: requestFactory('/sleep-timer'),
}

export const apiFactory = (slice, action) => (method, data) => async (dispatch, getState) => {
    const res = (await endpoints[slice](method, data)).data;
    if(method === 'GET') dispatch(action(res));
}