import React from 'react';
import { Grid, makeStyles } from '@material-ui/core'
import SleepTimerSwitch from './sleepTimerSwitch'
import SleepTimerSlider from './sleepTimerSlider';

const useStyles = makeStyles( () => ({
  root: {
    marginTop: '20px',
    marginBottom: '5px',
  }
}));

export default function SleepTimerController() {
  const classes = useStyles();

  return (
        <Grid container className={classes.root}> 
          <Grid item xs={4}>
            <SleepTimerSwitch />
          </Grid>
          <Grid item xs={8}>
            <SleepTimerSlider />
          </Grid>
        </Grid>
  );
}
